.recharge-check-pay { clear: both; border: 1px solid #d0d0d0; background-color: #ffffff; margin: 0 20px; padding: 10px; }
.recharge-check-pay-quick { padding: 16px 28px; background-color: #f7f7f7; }
.recharge-check-pay-bank { padding-top: 18px; padding-bottom: 22px; }
.recharge-check-pay-bank-choose { margin-left: 36px; color: #0091e6; cursor: pointer; padding-right: 12px; position: relative; font-weight: normal; }
.recharge-check-pay-bank-choose:after { content: ""; position: absolute; top: 7px; right: 0; border: 4px solid transparent; border-top: 6px solid #44c0fd; border-bottom: 0; }
.recharge-check-pay-title { float: left; font-weight: bold; }
.recharge-check-pay-float { float: left; margin-left: 30px; color: #999999; }
.recharge-check-pay-content { clear: both; padding: 16px 80px 0; line-height: 50px; }
.recharge-check-pay-add { float: left; margin-left: 14px; }
.recharge-check-pay-add-span { position: relative; padding-left: 18px; }
.recharge-check-pay-add-span:before { content: '+'; font-weight: bold; line-height: 0; font-size: 20px; position: absolute; top: 7px; left: 0; }
.recharge-check-mobile { overflow: hidden; }
.recharge-check-mobile-phone { float: left; font-size: 14px; font-weight: bold; }
.recharge-check-mobile-button { float: left; margin-left: 16px; }
.recharge-check-ident { margin-top: 30px; overflow: hidden; }
.recharge-check-ident-input { float: left; font-size: 14px; font-weight: bold; letter-spacing: 1px; padding: 0 10px; height: 28px; line-height: 28px; width: 66px; }
.recharge-check-ident-input.success { width: 46px; padding: 0 30px 0 10px; background: #fff url("../images/unit-1.png") 66px 8px no-repeat; }
.recharge-check-ident-input.error { border-color: #f34956; }
.recharge-check-ident-input:focus { border-color: #0091e6; }
.recharge-check-ident-click { float: left; margin-left: 24px; color: #0091e6; text-decoration: underline; cursor: pointer; }
.recharge-check-ident-tip { clear: both; margin-left: 140px; margin-top: 2px; color: #999999; }
.recharge-check-ident-tip:before { content: ''; display: inline-block; vertical-align: middle; position: relative; top: -1px; width: 12px; height: 12px; margin-left: 2px; margin-right: 5px; background-image: url("../../factory/pages/recharge/images/1.png"); }
.recharge-check-ident-tip.error:before { width: 14px; height: 14px; background-image: url("../../factory/pages/recharge/images/2.png"); }
.recharge-confirm-title { margin: 20px 25px 0; border: 1px solid #d0d0d0; background-color: #fff; padding: 6px 24px; }
.recharge-confirm-title-money { color: #f34956; float: right; line-height: 60px; margin-top: -60px; font-size: 16px; font-weight: bold; }
.recharge-confirm-line { margin: 30px 25px; line-height: 70px; }
.recharge-confirm-quick { position: relative; margin-left: 120px; background-color: #fff; border-bottom: 1px dashed #d0d0d0; }
.recharge-confirm-quick-word { float: left; font-weight: bold; margin-left: 22px; }
.recharge-confirm-quick .bank { margin-bottom: 0; margin-top: 10px; }
.recharge-confirm-quick-money { float: right; font-size: 16px; font-weight: bold; color: #f34956; margin-right: 22px; }
.recharge-confirm-quick-tip { position: absolute; top: 100%; right: 0; }
.recharge-confirm .payment { margin-top: 20px; }
.recharge-confirm-left { width: 110px; padding-right: 10px; float: left; text-align: right; min-height: 1px; }
.recharge-left { float: left; width: 130px; padding-right: 10px; text-align: right; font-size: 14px; min-height: 1px; }
.recharge-input { float: left; padding: 0 10px; height: 28px; line-height: 28px; width: 206px; }
.recharge-input.success { width: 186px; padding: 0 30px 0 10px; background: #fff url("../images/unit-1.png") 206px 8px no-repeat; }
.recharge-input.error { border-color: #f34956; }
.recharge-input:focus { border-color: #0091e6; }
.recharge-input-box { float: left; position: relative; overflow: hidden; z-index: 1; }
.recharge-input-box.cny:after { content: '￥'; position: absolute; top: 0; right: 10px; font-size: 14px; color: #999999; }
.recharge-button { float: left; }

.dialog-fail { line-height: 30px; }
.dialog-fail-content { position: relative; margin-top: 70px; margin-left: 190px; }
.dialog-fail-icon { position: absolute; top: -8px; left: -64px; width: 47px; height: 46px; background-image: url("../../factory/components/dialog-fail/1.png"); }
.dialog-fail-big { float: left; font-size: 24px; }
.dialog-fail-reason { float: left; font-size: 14px; line-height: 40px; }
.dialog-fail-tip { color: #999999; clear: both; margin-bottom: 20px; }
.dialog-fail-return { float: left; margin-left: -4px; }
.dialog-fail-a { float: left; margin-left: 22px; }


